@import "https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&display=swap";

html,
body,
#root,
// .dark-theme,
.light-theme {
  height: 100%;
}

a {
  cursor: pointer !important;
}

a.logo {
  float: left;
  height: 63px;
  margin: 1rem 2rem;
  width: 245px;
  text-align: left;
  overflow: hidden;

  > i {
    display: block;
    float: left;
    font-size: 64px;
    margin-left: -5px;
  }

  > i.upg {
    font-size: 168px;
    margin: -48px 0 0 7px;
  }
}

form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #fff;
  text-align: left;

  a {
    color: var(--up-brand-1-500);
    text-decoration: none;
  }
}

main {
  margin: auto;
  width: 36rem;
}

.bcg-img-logo {
  > i {
    position: fixed;
    z-index: -1;
    top: -2.75rem;
    bottom: unset;
    right: -5rem;
    width: 954px;
    height: 1156px;
    max-height: unset;
    max-width: unset;
    pointer-events: none;
    vertical-align: middle;
    border-style: none;

    > svg {
      width: 954px;
      height: 1156px;
    }
  }
}

.form-infobox {
  background-color: #fff;

  // > * {
  //   line-height: 1.25;
  // }
}

[class*="Fieldstyled__StyledField"] {
  &:-webkit-autofill {
    box-shadow: 0 0 0 30px white inset !important;
    &:hover,
    &:focus,
    &:active {
      box-shadow: 0 0 0 30px white inset !important;
    }
  }
}

.cdk-overlay-pane {
  z-index: 1;
}

.page-content {
  display: flex;
  grid-auto-rows: 1fr;
  min-height: calc(100vh - 64px);

  main {
    > .page-title,
    > .page-main-content {
      padding: 24px 48px;
    }

    > .page-main-content {
      min-height: calc(100vh - 215px);
    }

    .page-title {
      background-color: #fff;
      padding: 16px 48px;
    }

    width: 100%;
    margin: 0;

    .footer {
      padding: 0;
      padding-top: 10px;
      clear: both;
      width: 100%;
      padding-right: 15px;
      padding-left: 15px;
      margin-right: auto;
      margin-left: auto;

      footer {
        height: 72px;
        display: -webkit-flex;
        display: flex;
        background-color: var(--up-neutral-200);
        border-top: 1px solid var(--up-neutral-400);

        .footer-container {
          height: 72px;
          display: -webkit-flex;
          display: flex;
          background-color: var(--up-neutral-200);
          border-top: 1px solid var(--up-neutral-400);
        }

        ul {
          display: -webkit-inline-flex;
          display: inline-flex;
          list-style: none;
          margin-left: 33px;
          padding: 0;
          -webkit-justify-content: center;
          justify-content: center;
          -webkit-align-items: center;
          align-items: center;

          li {
            margin-right: 48px;

            a {
              font-weight: 400;
              color: var(--up-neutral-700);
              text-decoration: none;

              // font-family: Source Sans Pro,serif;
              // color: var(--up-coreTxt);
              font-weight: 400;
              margin: 0;
              font-size: 14px;
              line-height: 21px;
            }
          }
        }
      }
    }
  }
}
