.UpTopBarComponent {
  display: flex;
}

.up-top-bar-container {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  height: 64px;
  -webkit-flex-direction: row;
  flex-direction: row;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  -webkit-align-items: center;
  align-items: center;
  background-color: var(--up-brand-1);
  color: white;
}

.up-top-bar-logo-container {
  margin-left: 17px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: center;
  justify-content: center;
}

.community-logo,
.new-design-logo {
  width: 130px;
  height: 30px;
  background: var(--community-logo);
  background: url(https://dvd27p5isdjym.cloudfront.net/community/banquprs/images/white_logo_banquprs_ab05da92-db04-4ba9-9a26-a2e1ac78aa6a.png)
    center no-repeat;
  background-size: contain !important;
  background-repeat: no-repeat !important;
  cursor: pointer;
  height: 38px;
}

.UpTopBarComponent .up-top-bar-container {
  .up-tob-bar-page-details {
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-align-items: center;
    align-items: center;
    margin-right: 44px;
    -webkit-flex-direction: row;
    flex-direction: row;
  }

  .up-top-bar-page-title {
    display: -webkit-flex;
    display: flex;
  }

  .up-tob-bar-page-details {
    display: -webkit-flex;
    display: flex;

    .up-top-bar-profile-area {
      &.up-top-bar-profile-area-with-menu {
        cursor: pointer;
        position: relative;

        > i {
          display: inline-flex;
          margin-left: 4px;
        }

        .cdk-overlay-pane {
          position: absolute;
          top: 32px;
          right: 0;

          .up-user-menu-details {
            white-space: nowrap;
            display: -webkit-flex;
            display: flex;
            -webkit-justify-content: center;
            justify-content: center;
            -webkit-flex-wrap: wrap;
            flex-wrap: wrap;

            padding-left: 16px;
            padding-left: calc(4 * var(--up-spacing-base-unit));

            padding-right: 16px;
            padding-right: calc(4 * var(--up-spacing-base-unit));

            padding-top: 16px;
            padding-top: calc(4 * var(--up-spacing-base-unit));

            .up-user-menu-avatar-container {
              position: relative;

              .up-avatar_change_photo_container {
                margin: 0;
                padding: 0;
                height: 0;

                button {
                  margin-left: -10px;

                  background: none;
                  border: none;
                  color: var(--up-brand-1-700);

                  a.up-avatar_change_photo {
                    position: absolute;
                    top: 28px;
                    left: 28px;
                    background: var(--up-white);
                    border-radius: 50px;
                    cursor: pointer;
                    padding: calc(1 * var(--up-spacing-base-unit));
                  }
                }
              }
            }

            .up-padding-bottom-4 {
              padding-bottom: 16px;
              padding-bottom: calc(4 * var(--up-spacing-base-unit));
            }

            .up-avatar-user-large {
              background-size: cover;
              background-position: center;
              width: 48px;
              height: 48px;
            }

            .up-user-menu-details .up-user-details {
              display: -webkit-flex;
              display: flex;
              -webkit-flex-direction: column;
              flex-direction: column;
              text-align: center;
              -webkit-flex: 100%;
              flex: 100%;
            }

            .up-title-S,
            h3.up-notification-title-info,
            h3.up-notification-title-warning,
            h3.up-notification-title-error,
            h3.up-notification-title-success {
              //   font-family: Source Sans Pro, serif;
              color: var(--up-coreTxt);
              font-weight: 400;
              margin: 0;
              font-weight: 700;
              font-size: 16px;
              line-height: 20px;
              text-align: center;
            }

            .up-user-menu-details[_ngcontent-rol-c142]
              .up-user-details[_ngcontent-rol-c142]
              .up-user-email[_ngcontent-rol-c142] {
              color: var(--up-neutral-700);
            }
            .up-paragraph-XS {
              font-family: Source Sans Pro, serif;
              color: var(--up-coreTxt);
              font-weight: 400;
              margin: 0;
              font-size: 12px;
              line-height: 18px;
            }
          }

          .up-dropdown-separator,
          .up-pop-out-separator {
            display: block;
            width: 100%;
            height: 1px;
            margin: 16px 0;
            background: var(--up-neutral-400);
          }
        }
      }

      height: 100%;
      display: -webkit-flex;
      display: flex;
      margin-left: 28px;
      -webkit-flex-direction: row;
      flex-direction: row;
      -webkit-align-items: center;
      align-items: center;
    }
  }
}

.up-avatar-dark-mode {
  background-color: var(--up-brand-1-700);
  border: 1px solid var(--up-brand-1-400);
}

.up-avatar-user-default,
.up-avatar-user-large,
.up-avatar-user-small {
  display: -webkit-flex;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--up-brand-1-50);
  user-select: none;
  width: 32px;
  height: 32px;
  border: 1px solid var(--up-brand-1-100);
  border-radius: 50px;
  background-size: cover;
  background-position: center;
  background-color: var(--up-brand-1-900);
  border: 1px solid var(--up-brand-1-400);
}

.up-avatar-dark-mode {
  display: -webkit-flex;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--up-brand-1-50);
  user-select: none;
  width: 32px;
  height: 32px;
  border: 1px solid var(--up-brand-1-100);
  border-radius: 50px;
  background-size: cover;
  background-position: center;
  background-color: var(--up-brand-1-900);
  border: 1px solid var(--up-brand-1-400);

  p {
    color: var(--up-white);
    font-weight: 600;
  }
}

.up-avatar-user-default p,
.up-avatar-user-large p,
.up-avatar-user-small p,
.up-avatar-dark-mode p {
  color: white;
  font-weight: 400;
  margin: 0;
  font-size: 14px;
  line-height: 21px;
}

.top-bar-container .up-top-bar-right-content .up-tob-bar-right-icon-container {
  position: relative;
  cursor: pointer;
}

.up-tob-bar-right-icon-container {
  margin-left: 16px;
}

.up-badge {
  display: -webkit-flex;
  display: flex;
  margin-left: auto;

  .up-badge-default,
  .up-badge-inactive,
  .up-badge-active,
  .up-badge-tabs-grey,
  .up-badge-tabs-white,
  .up-badge-subtle {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-align-items: center;
    align-items: center;
    width: auto;
    min-width: 24px;
    height: 24px;
    border-radius: 50px;
    border: 2px solid var(--up-brand-1-600);
    margin-right: 8px;
    background-color: var(--up-brand-1-400);
  }

  .up-badge-notification {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-align-items: center;
    align-items: center;
    width: auto;
    min-width: 24px;
    height: 24px;
    border-radius: 50px;
    border: 2px solid var(--up-brand-1-600);
    margin-right: 8px;
    background-color: var(--up-brand-1-400);

    p.up-badge-text {
      color: var(--up-brand-1);
    }

    background-color: var(--up-brand-3);
    border: 2px solid var(--up-brand-1);
  }
}

.top-bar-container .up-top-bar-right-content .up-tob-bar-right-icon-container {
  .up-badge-notification-container {
    position: absolute;
    top: -11px;
    left: 10px;
  }

  position: relative;
  cursor: pointer;

  .up-top-bar-bell-container {
    display: block;

    i {
      color: var(--up-brand-1-50);
      width: 24px;
      height: 24px;
      display: flex;
    }
  }
}
